.homePopular{
padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .section-h2{
    margin-bottom: 20px;
    z-index: 1;
    position: relative;
  }

  &-items{
    display: flex;
    flex-wrap: wrap;
    padding-left: 10px;
    position: relative;
    z-index: 1;
    justify-content: center;
    margin: 0 -10px;
    width: calc(100% + 20px);

    @media screen and (min-width: 960px) {
      padding-left: 0;

      &__item{
        width: calc(50% - 20px);
      }

    }

    @media screen and (min-width: 1152px) {
      margin: 0 -30px;
      &__item{
        max-width: 560px;
        width: calc(50% - 30px);
        margin: 15px;
      }




    }

    &__item{
      max-width: 450px;
      width: calc(100% - 20px);
      margin: 10px;
      background: #FFFFFF;
      box-shadow: 0px 6px 15px rgba(140, 82, 255, 0.3);
      border-radius: 10px;
      padding: 15px 10px 10px;
      position: relative;
      min-height: 180px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      position: relative;

      & > * {
        z-index: 1;
        position: relative;
      }

      .image{
        position: static;
      }

      .image img{
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: bottom center;
        border-radius: 0 10px 10px 0;
      }

      .title{
        max-width: 230px / 290px * 100%;
        font-size: 15px;
        line-height: 130%;
        letter-spacing: 0.05em;
        margin-bottom: 10px;

        span{
          &.blue{
            color: #5271FF;
          }

          &.violet{
            color: #8C52FF;
          }

          &.pink{
            color: #CB6CE6
          }
        }

      }

      .caption{
        font-size: 13px;
        line-height: 130%;
        letter-spacing: 0.05em;
        color: #FFFFFF;
        padding: 8px 12px;
        margin-bottom: 8px;
        margin-left: -20px;
        max-width: 195px / 290px * 100%;
        position: relative;

        &-with-image{
          margin-left: 0;
          max-width: 156px / 290px * 100%;
          padding: 0;
          img{
            height: auto;
            width: 100%;
          }
        }

        &-blue{
          background: #5271FF;
          border-radius: 10px;
        }

        &-violet{
          background: #8C52FF;
          border-radius: 10px;
        }

        &-pink{
          background: #CB6CE6;
          border-radius: 10px;
        }

        @media screen and (min-width: 640px) {
          padding: 12px 16px;
          font-size: 15px;
        }

        @media screen and (min-width: 1152px) {
          padding: 14px 26px;
          font-size: 17px;
        }

      }

      .price{
        margin-top: auto;
        display: flex;
        align-items: center;
        margin-bottom: 12px;
        max-width: 180px / 290px * 100%;
        &-old{
          font-size: 13px;
          line-height: 150%;
          letter-spacing: 0.05em;
          text-decoration-line: line-through;
          color: #828282;
          margin-right: 10px;
        }
        &-new{
          font-size: 17px;
          line-height: 150%;
          letter-spacing: 0.05em;

          &.blue{
            color: #5271FF;
          }

          &.violet{
            color: #8C52FF;
          }

          &.pink{
            color: #CB6CE6
          }

        }

        @media screen and (min-width: 1152px) {
          margin-bottom: 32px;
        }

      }

      .btn{
        max-width: 115px;
        color: #5271FF;

        &-blue{
          color: #5271FF;
        }

        &-violet{
          color: #8C52FF;
        }

        &-pink{
          color: #CB6CE6;
        }

        @media screen and (min-width: 640px) {
          padding: 11px 24px;
          font-size: 15px;
        }

        @media screen and (min-width: 1152px) {
          padding: 14px 32px;
          font-size: 17px;
          line-height: 23px;
          max-width: none;
        }

      }

      @media screen and (min-width: 480px) {
        .title,
        .price{
          font-size: 18px;
        }

        .price{
          &-old{
            font-size: 18px;
          }
          &-new{
            font-size: 22px;
          }
        }
        
      }

      @media screen and (min-width: 1152px) {
        padding: 22px 20px 26px 20px;

        .caption{
          margin-left: -30px;
        }

        .title,
        .price{
          font-size: 22px;
        }

        .price{
          &-old{
            font-size: 20px;
          }
          &-new{
            font-size: 25px;
          }
        }

        
      }

    }

  }

}