.homeCatalog{
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: calc(100% - 60px);
    left: -20px;
    z-index: -1;
    width: 159px;
    height: 146px;

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      top: calc(100% - 70px);
      right: -25px;
      width: 186px;
      height: 172px;
    }

    @media screen and (min-width: 640px) {
      width: 307px;
      height: 279px;
      top: 100%;
      transform: translateY(-55%);
    }

    @media screen and (min-width: 1160px) {
      width: 377px;
      height: 327px;
      left: -160px;
    }

  }

  .container{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .section-h2{
    margin-bottom: 20px;
    position: relative;
    z-index: 1;
  }

  &-items{
    display: flex;
    flex-wrap: wrap;
    margin: 0 -5px;
    width: calc(100% + 10px);
    max-width: 480px;

    &__item{
      width: calc(50% - 10px);
      margin: 10px 5px;
      display: flex;
      background: #FFFFFF;
      box-shadow: 0px 6px 15px rgba(140, 82, 255, 0.3);
      border-radius: 10px;
      position: relative;

      &.djolly{
        .item-text__name{
          br{
            display: none;


            @media screen and (min-width: 480px) {
              display: block;
            }

          }
        }
      }

      .item-text{
        width: 69%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        z-index: 1;
        padding: 20px 10px 10px;
        min-height: 145px;
      }

      .item-text__name{
        font-size: 11px;
        line-height: 130%;
        letter-spacing: 0.05em;
        color: #262626;
        margin-bottom: 15px;
      }

      .item-text__price{
        font-size: 10px;
        line-height: 150%;
        margin-bottom: auto;

        span.blue{
          font-size: 16px;
          line-height: 150%;
          vertical-align: middle;
        }

      }

      .item-btn{
        opacity: 1;
        margin-top: 15px;
        font-size: 11px;
        padding: 8px 11px;


        @media screen and (min-width: 480px) {
          font-size: 13px;
        }

        @media screen and (min-width: 640px) {
          padding: 11px 20px;
          font-size: 15px;
        }

        @media screen and (min-width: 1152px) {
          padding: 11px 20px;
          font-size: 17px;
          line-height: 23px;

        }

      }

      .item-image{
        margin-left: auto;
        max-width: 144px;
        flex-shrink: 0;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;

        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 0 10px 10px 0;
          object-position: left;
        }

      }

      &.fullwidth{
        width: calc(100% - 10px);

        .item-text__name{
          font-size: 16px;
          line-height: 110%;
        }

      }

    }

  }

  @media screen and (min-width: 480px) {
    &-items{
      width: calc(100% + 20px);
      margin: 0 -10px;
      &__item{
        width: calc(50% - 20px);
        margin: 10px;
        &.fullwidth{
          width: calc(100% - 20px);
        }
        .item-btn{
          opacity: 1;
          margin-top: 15px;
        }
        .item-image{
          max-width: 200px;
        }
        .item-text{
          padding: 20px 10px;
          width: 77%;
        }
        .item-text__name{
          font-size: 14px;
        }
        &.fullwidth .item-text__name{
          font-size: 25px;
        }
        .item-text__price{
          font-size: 14px;
          span.blue{
            font-size: 22px;
          }
        }
      }
    }

  }

  @media screen and (min-width: 640px) {
    &-items{
      &__item{

        .item-image{
          max-width: 200px;
        }
      }
    }
  }

  @media screen and (min-width: 960px) {

    &-items{
      max-width: none;
    }

    &-items__item{
      flex: 1 1 210px;

      .item-text{
        width: 79%;
      }

      &:nth-child(1){
        order: 1;
      }
      &:nth-child(2){
        order: 2;
      }
      &:nth-child(3){
        order: 3;
      }
      &:nth-child(4){
        order: 5;
      }
      &:nth-child(5){
        order: 4;
      }
      &:nth-child(6){
        order: 6;
      }
      &:nth-child(7){
        order: 7;
      }
      &:nth-child(8){
        order: 8;
      }
      &:nth-child(9){
        order: 9;
      }
      &:nth-child(10){
        order: 10;
      }
      &:nth-child(11){
        order: 11;
      }
      &:nth-child(12){
        order: 12;
      }
      &:nth-child(13){
        order: 13;
      }


      
    }

    &-items__item.fullwidth{
      flex: 1 1 450px;
    }

    
  }

  @media screen and (min-width: 1152px) {

    &-items__item{
      flex: 1 1 265px;
    }

    &-items__item.fullwidth{
      flex: 1 1 560px;
    }

    &-items{
      &__item{
        min-height: 260px;

        .item-image{
          max-width: 250px;
        }

        .item-text{
          padding: 30px 10px;
        }
        .item-text__name{
          font-size: 17px;
          margin-bottom: auto;
        }
        &.fullwidth .item-text__name{
          font-size: 34px;
        }

        .item-btn{
          font-size: 17px;
          line-height: 23px;
        }

        .item-text__price{
          margin-top: 20px;
          font-size: 17px;
          span.blue{
            font-size: 32px;
          }
        }
      }
    }

    
  }


}