.homeAbout {
  padding: 40px 0 0px;
  background: #fff;

  .container {
    position: relative;
  }

  &-figure {
    display: none;

    @media screen and (min-width: 1440px) {
      display: block;
      position: absolute;
      bottom: -10px;
      left: -10px;
      transform: translateX(-100%);
      z-index: 1;
    }

  }

  @media screen and (min-width: 830px) {
    padding-bottom: 35px;
  }

  .container {
    display: flex;
    flex-wrap: wrap;
  }

  &-text {
    margin: 0 auto;
    margin-bottom: 23px;

    &__title {
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 15px;
    }

    &__desc {
      text-align: center;
      font-size: 16px;
      line-height: 150%;
      letter-spacing: 0.02em;
      color: #262626;
      margin-bottom: 20px;
    }

    &__btns {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      max-width: 230px;
      margin: 0 auto;

      .btn {
        margin: 7px 0;
        width: 100%;
        max-width: 230px;
        padding: 16px 18px;
      }

      @media screen and (min-width: 480px) {
        max-width: 460px;
        justify-content: space-between;
        align-items: stretch;
        .btn {
          width: calc(100% / 2 - 10px);
        }
      }

      @media screen and (min-width: 830px) {
        margin: 0 auto 0 0;
      }

    }
  }

  &-image {
    position: relative;
    left: 50%;
    width: 100vw;
    margin-left: -50vw;
    max-height: 772px;

    &__img {


      img {
        height: 100%;
        width: auto;
        margin: 0 auto;
        object-fit: contain;
      }
    }

    &__tooltips {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

      .tooltip {
        position: absolute;
        font-size: 11px;
        line-height: 130%;
        letter-spacing: 0.05em;
        color: #262626;
        padding: 4px 8px;
        background: #FFFFFF;
        box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
        border-radius: 5px;

        @media screen and (min-width: 480px) {
          font-size: 13px;
          max-width: 170px !important;
        }

        @media screen and (min-width: 640px) {
          &::after {
            width: 30px;
            height: 30px;
          }
        }

        @media screen and (min-width: 960px) {
          &::after {
            width: 35px;
            height: 35px;
          }
        }


        &.wrinkles {
          top: 93px / 460px * 100%;
          left: 146px / 320px * 100%;
        }

        &.skin {
          left: 205px / 320px * 100%;
          top: 207px / 460px * 100%;
        }

        &.tone {
          right: 230px / 320px * 100%;
          bottom: 190px / 460px * 100%;
          max-width: 80px;
        }

        &.oval {
          bottom: 120px / 460px * 100%;
          right: 220px / 320px * 100%;
          max-width: 90px;
        }

        &.chin {
          left: 170px / 320px * 100%;
          bottom: 88px / 460px * 100%;
          transform: translateY(-50%);
          max-width: 150px;
          width: 100%;
        }

        &.relief {
          left: 170px / 320px * 100%;
          bottom: 44px / 460px * 100%;
          transform: translateY(-50%);
          max-width: 150px;
          width: 100%;
        }

        &-bottom-left {
          &::after {
            bottom: -10px;
            left: -10px;
            @media screen and (min-width: 640px) {
              bottom: -15px;
              left: -15px;
            }
            @media screen and (min-width: 960px) {
              bottom: -20px;
              left: -20px;
            }
          }
        }

        &-top-left {
          &::after {
            top: -10px;
            left: -10px;
            @media screen and (min-width: 640px) {
              top: -15px;
              left: -15px;
            }
            @media screen and (min-width: 960px) {
              top: -20px;
              left: -20px;
            }
          }
        }

        &-top-right {
          &::after {
            top: -10px;
            right: -10px;
            @media screen and (min-width: 640px) {
              top: -15px;
              right: -15px;
            }
            @media screen and (min-width: 960px) {
              top: -20px;
              right: -20px;
            }
          }
        }


        &::after {
          position: absolute;
          content: "";
          width: 20px;
          height: 20px;
          background-size: 100%;
          background-repeat: no-repeat;
          background-image: url("data:image/svg+xml,%3Csvg width='20' height='21' viewBox='0 0 20 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.0917 20.0686C15.5963 20.0686 20 15.6649 20 10.1603C20 4.47227 15.5963 0.0686035 10.0917 0.0686035C4.40367 0.0686035 0 4.47227 0 10.1603C0 15.6649 4.40367 20.0686 10.0917 20.0686Z' fill='%238C52FF' fill-opacity='0.2'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.0891 17.867C14.3093 17.867 17.7955 14.3807 17.7955 10.1605C17.7955 5.75687 14.3093 2.27063 10.0891 2.27063C5.68546 2.27063 2.19922 5.75687 2.19922 10.1605C2.19922 14.3807 5.68546 17.867 10.0891 17.867Z' fill='%238C52FF' fill-opacity='0.2'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.0892 14.3806C12.4745 14.3806 14.3094 12.5457 14.3094 10.1604C14.3094 7.59158 12.4745 5.75671 10.0892 5.75671C7.52041 5.75671 5.68555 7.59158 5.68555 10.1604C5.68555 12.5457 7.52041 14.3806 10.0892 14.3806Z' fill='%238C52FF'/%3E%3C/svg%3E%0A");
        }
      }
    }
  }

  @media screen and (min-width: 830px) {

    .container {
      align-items: center;
    }

    &-text {
      width: 40%;
      margin: 0 auto 0 0;
      position: relative;
      z-index: 1;

      & > * {
        text-align: left;
      }

      &__btns {
        .btn {
          max-width: 340px;
          flex: 1 1 260px;
        }

      }

    }
    &-image {
      width: 55%;
      left: auto;
      transform: none;

      &__img {
        margin: 0;
      }
    }
  }

  @media screen and (min-width: 960px) {
    &-text {
      width: 412px / 960px * 100%;
      position: relative;
      z-index: 1;

      &__btns {
        .btn {
          font-size: 16px;
          line-height: 21px;
        }
      }

    }
    &-image {
      width: 847px / 960px * 100%;
      margin-right: -174px;

      &__tooltips {
        .tooltip {
          &.tone {
            right: 64%;
          }

          &.oval {
            right: 60%;
          }
        }
      }

    }
  }

  @media screen and (min-width: 1280px) {

    .container {
      align-items: flex-start;
    }

    &-text {
      width: 760px / 1290px * 100%;
      position: relative;
      z-index: 1;
      padding-top: 83px;

      .section-h1 {
        margin-bottom: 39px;
      }

      &__desc {
        font-size: 20px;
        margin-bottom: 70px;
      }

      &__btns {
        .btn {
          font-size: 20px;
          line-height: 27px;
          padding: 23px;
        }
      }

    }
    &-image {
      width: 1020px / 1290px * 100%;
      margin-right: -229px;

      &__tooltips {
        .tooltip {
          max-width: 190px !important;
          font-size: 15px;
          padding: 8px;

          &.tone {
            right: 64%;
          }

          &.oval {
            right: 60%;
          }
        }
        .tooltip.wrinkles{
          top: 149px / 772px * 100%;
          left: 536px / 939px * 100%;
        }
        .tooltip.skin{
          top: 367px / 772px * 100%;
          left: 611px / 939px * 100%;
        }
        .tooltip.chin{
          top: 592px / 772px * 100%;
          left: 544px / 939px * 100%;
          bottom: auto;
        }
        .tooltip.relief{
          top: 666px / 772px * 100%;
          left: 301px / 939px * 100%;

          &::after{
            left: auto;
            right: -20px;
          }
        }
      }

    }

  }


}