.homeForm{
  padding: 0 0 70px;
  position: relative;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 40px 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 100%;
    left: -20px;
    z-index: -1;
    width: 195px;
    height: 229px;
    transform: translateY(-50%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      top: 0;
      width: 287px;
      height: 279px;
      left: -25px;
      transform: translateY(-60%);
    }

    @media screen and (min-width: 640px) {
      width: 426px;
      height: 421px;
      top: 100%;
      left: -90px;
      transform: translateY(-55%);
    }

    @media screen and (min-width: 1160px) {
      width: 507px;
      height: 479px;
      top: 50%;
      left: -65px;
      transform: translateY(-28%);
    }

  }

  &-block{
    background: #CA6BE5;
    border-radius: 10px;
    padding: 30px 40px;
    position: relative;
    overflow: hidden;

    &__image{
      display: none;

    }

    &__title{
      text-align: center;
      margin-bottom: 5px;
      color: #fff;
    }

    &__desc{
      text-align: center;
      color: #fff;
      margin-bottom: 30px;
      font-size: 13px;
      line-height: 150%;
    }

    &__form{
      max-width: 230px;
      margin: 0 auto;
      .form__field{
        span.label{
          color: #fff;
        }
      }

      .form__agreement{
        margin-top: 20px;
        text-align: center;
        font-size: 10px;
        line-height: 130%;
        color: #FFFFFF;
        a{
          color: #fff;
          text-decoration: underline;
        }
      }

      .btn{
        padding: 16px 25px;
        position: relative;
        border-radius: 15px;
        &::after{
          content: "";
          position: absolute;
          display: block;
          width: 100%;
          height: 100%;
          background: #E1CAE8;
          box-shadow: 0px 3px 25px rgba(0, 0, 0, 0.35);
          border-radius: 15px;
          z-index: -1;
          transform: translateY(3px);
        }
      }

    }

    @media screen and (min-width: 480px){

      display: flex;
      flex-direction: column;
      align-items: flex-end;
      padding-right: 10px;

      & > *{
        position: relative;
        z-index: 1;
        max-width: 235px;
      }

      &__title,
      &__desc{
        text-align: right;
      }

      &__title{
        font-size: 20px;
      }

      &__desc{
        font-size: 16px;
      }

      &__form{
        margin: 0 0 0 auto;

        .btn{
          font-size: 13px;
        }

      }

      .form__field,
      .form__submit{

        input,
        button{
          box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
        }

      }

      &__image{
        display: block;
        position: absolute;
        top: 47px;
        left: 0;
        bottom: 0;
        max-width: 246px;
        width: 100%;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

    }

    @media screen and (min-width: 640px){
      padding-right: 20px;

      .form__field,
      .form__submit{

        span.label{
          font-size: 16px;
        }

        input{
          font-size: 16px;
        }

        input,
        button{
          height: 60px;
        }

      }

      .form__agreement{
        font-size: 12px;
      }

      .btn{
        font-size: 16px;
        line-height: 21px;
        padding: 17px 26px;
      }

      & > *{
        max-width: 260px;
      }

      &__title{
        font-size: 30px;
      }

      &__title,
      &__desc{
        text-align: right;
        max-width: 330px;
      }

      &__image{
        max-width: 296px;

        img{
          object-position: right;
        }
      }

    }

    @media screen and (min-width: 960px){
      padding-left: 474px;
      align-items: center;
      padding-right: 68px;
      overflow: visible;

      &__title,
      &__desc{
        text-align: center;
        max-width: 330px;
      }

      &__form{
        margin: 0 auto;
      }

      &__image{
        max-width: 474px;
        top: -17px;

        img{
          object-fit: contain;
          object-position: right;
        }
      }

    }

    @media screen and (min-width: 1240px) {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding-left: 370px;
      padding-right: 30px;
      border-radius: 20px;

      &__image{
        max-width: 340px;
        top: -84px;
      }

      &__title{
        font-size: 40px;
      }

      &__title,
      &__desc{
        max-width: 100%;
        width: 100%;
        text-align: left;
      }

      &__form{
        display: flex;
        flex-wrap: wrap;
        max-width: 100%;
        width: 100%;
        align-items: flex-end;
        .form__field{
          margin-bottom: 0;
          flex: 1 1 200px;
          margin-right: 20px;

          input{
            font-size: 17px;
          }

        }
        .form__submit{
          flex: 1 1 300px;
          .btn{
            font-size: 20px;
            padding: 16px 18px;
          }
        }
        .form__agreement{
          width: 100%;
          text-align: left;
          font-size: 14px;
        }
      }

    }

  }

}